body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: #f9f9f9;
}

.App-Wrapper {
  max-width: 375px;
  margin: 0 auto;
  background: #fff;
  min-height: 700px;
}

.searchBox {
  position: relative;
  box-sizing: border-box;
  padding: 40px 20px;
  text-align: center;
  font-size: 0.8em;
  color: #8f8f8f;
  background-color: #f9f9f9;
  box-shadow: 0 4px 10px 0 #f2f2f2;
}

.searchBox span {
  font-weight: bold;
}

.searchBox .inputBox {
  position: relative;
}

.searchBox .inputBox input {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 10px 20px;
  text-align: center;
  font-size: 1em;
  font-weight: bold;
  color: #8f8f8f;
  border: none;
  border-bottom: 1px solid #dedede;
  background: none;
  
}

.searchBox .inputBox input:focus {
  outline: none;
}

.searchBox .searchBtn {
  width: 100%;
  margin: 34px 0 0 0;
  padding: 10px;
  font-size: 1.2em;
  font-weight: bold;
  color: #2278e7;
  border: 1px solid #2278e7;
  background: none;
  border-radius: 30px;
}

.searchBox .searchBtn:focus {
  outline: none;  
}

.searchBox .searchBtn:hover {
  cursor: pointer;
}

.jobsWrapper {
  padding: 10px 20px;
}

.jobsWrapper .numberOfJobs {
  padding: 16px 0;
  font-size: 0.8em;
  font-weight: bold;
  color: #353535;
  border-bottom: 1px solid #e6e6e6;
}
.Header {
  padding: 10px 20px;
  border-bottom: 1px solid #d8d8d8;
}
.clearFix {
    display: block;
    clear: both;
}

.jobCard {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    padding: 16px 0;
    font-size: 0.9em;
    color: #353535;
    border-bottom: 1px solid #e6e6e6;
}

.jobCard .jobTitle {
    display: inline-block;
    position: relative;
    width: 60%;
    font-weight: bold;
}

.jobCard .salaryRange {
    display: inline-block;
    position: relative;
    width: 40%;
    font-weight: bold;
    text-align: right;
    vertical-align: top;
}

.jobCard .jobDetails {
    display: block;
    position: relative;
    padding: 10px 0;
}

.jobCard .jobDetails .icon {
    display: inline-block;
    min-width: 14px;
    width: 14px;
    height: 14px;
    vertical-align: middle;
    margin-right: 10px;
    border: 1px solid #2278e7;
}

.jobCard .jobDetails .text {
    display: inline-block;
    vertical-align: middle;
    padding-right: 6px;
}

.jobCard .jobDetails #location,
.jobCard .jobDetails #xpLvl,
.jobCard .jobDetails #degree,
.jobCard .jobDetails #type {
    display: inline-block;
    padding: 8px 0;
    width: 40%;
}

.jobCard .jobDetails > div > div {
    display: flex;
}

.jobCard .company {
    display: inline-block;
    width: 70%;
}

.jobCard .company>div {
    display: flex;
}

.jobCard .company .logo {
    display: flex;
    flex: 1 1;
    width: 30px;
    height: 30px;
    vertical-align: middle;
    margin-right: 10px;
    /* border: 1px solid #e6e6e6;
    -webkit-border-radius: 6px;
    border-radius: 6px; */
}

.jobCard .company .logo img {
    display: block;
    max-width: 100%;
    max-height: 100%;
    height: auto;
}

.jobCard .company .name {
    display: flex;
    flex: 5 1;
    vertical-align: middle;
    width: auto;
}

.jobCard .time {
    display: inline-block;
    width: 28%;
    vertical-align: bottom;
    color: #e6e6e6;
    text-align: right;
}
